import 'babel-polyfill';
import Vue from 'vue';
import App from './components/App.vue';
import store from './store';
import { currency } from './currency';
Vue.filter('currency', currency);
new Vue({
    el: '#app',
    store: store,
    render: function (h) { return h(App); }
});



